import React, { useEffect, useState, useRef, useCallback } from 'react'
import { useWS } from './WSProvider'
import { X, Send } from 'lucide-react'
// lightweight timestamp formatting without extra deps

export default function HelpdeskChat({ request, user, onClose }: { request: any, user: any, onClose?: () => void }) {
  const ws = (() => { try { return useWS() } catch { return null } })()
  const [messages, setMessages] = useState<any[]>(request?.messages || [])
  const [input, setInput] = useState('')
  const [typingUsers, setTypingUsers] = useState<Record<string, number>>({})
  const msgsRef = useRef<HTMLDivElement | null>(null)
  const typingTimeoutRef = useRef<number | null>(null)

  useEffect(() => {
    if (!ws) return
    const un = ws.addListener((data: any) => {
      try {
        if (data?.type === 'help-message' && String(data.requestId) === String(request.id)) {
          setMessages((m) => [...m, data.message])
        }
        if (data?.type === 'help-request-updated' && data.request?.id === request.id) {
          // update messages if any
          setMessages(data.request.messages || [])
        }
        if (data?.type === 'help-typing' && String(data.requestId) === String(request.id)) {
          const who = data.fromName || data.fromEmail || (data.admin ? 'admin' : 'user')
          setTypingUsers(prev => ({ ...prev, [who]: Date.now() }))
        }
      } catch {}
    })
    return () => { un() }
  }, [ws, request?.id])

  const sendMsg = () => {
    if (!input.trim()) return
    const payload = { type: 'help-message', requestId: request.id, message: input }
    try { ws?.send(payload) } catch {}
    // optimistic local echo
    const msgObj = { fromEmail: user?.email || null, fromName: user?.username || null, message: input, ts: Date.now(), admin: !!(user?.email && user?.isAdmin) }
    setMessages(m => [...m, msgObj])
    setInput('')
  }

  // auto-scroll to bottom when messages update
  useEffect(() => {
    if (!msgsRef.current) return
    // scroll to bottom
    try {
      msgsRef.current.scrollTop = msgsRef.current.scrollHeight
    } catch {}
  }, [messages])

  // send typing notifications (debounced)
  const sendTyping = useCallback(() => {
    try {
      ws?.send({ type: 'help-typing', requestId: request.id, fromName: user?.username, fromEmail: user?.email, admin: !!user?.isAdmin })
    } catch {}
    if (typingTimeoutRef.current) {
      clearTimeout(typingTimeoutRef.current)
    }
    // clear local typing indicator after 3s
    typingTimeoutRef.current = window.setTimeout(() => {
      setTypingUsers(prev => {
        const copy = { ...prev }
        delete copy[user?.username || user?.email || 'me']
        return copy
      })
    }, 3000)
  }, [ws, request.id, user])

  // cleanup timeout on unmount
  useEffect(() => {
    return () => { if (typingTimeoutRef.current) clearTimeout(typingTimeoutRef.current) }
  }, [])

  return (
    <div className="fixed inset-0 z-50 flex items-end justify-center p-4">
      <div className="absolute inset-0 bg-black/40" onClick={onClose} />
      <div className="relative w-full max-w-xl bg-slate-900 rounded-lg shadow-xl overflow-hidden">
        <div className="flex items-center justify-between p-3 border-b border-slate-700">
          <div className="font-semibold">Helpdesk — {request.username || 'Anonymous'}</div>
          <div className="flex items-center gap-2">
            <button className="px-2 py-1 rounded bg-neutral-700 text-sm" onClick={onClose}><X className="w-4 h-4"/></button>
          </div>
        </div>
        <div ref={msgsRef} className="h-72 overflow-y-auto p-3 space-y-2 bg-black/20">
          {messages.map((m, i) => (
            <div key={i} className={`max-w-[80%] px-3 py-2 rounded ${m.admin ? 'bg-emerald-600 text-white ml-auto' : 'bg-slate-700 text-slate-100'}`}>
              <div className="flex items-center justify-between">
                <div className="text-xs opacity-70 mb-1">{m.fromName || m.fromEmail || (m.admin ? 'Admin' : 'User')}</div>
                <div className="text-xs opacity-50 ml-2">{m.ts ? new Date(m.ts).toLocaleTimeString([], { hour: '2-digit', minute: '2-digit' }) : ''}</div>
              </div>
              <div className="whitespace-pre-wrap break-words">{m.message}</div>
            </div>
          ))}

          {/* typing indicators */}
          {Object.keys(typingUsers).length > 0 && (
            <div className="text-xs text-slate-300 opacity-80">{Object.keys(typingUsers).join(', ')} typing...</div>
          )}
        </div>
        <div className="p-3 border-t border-slate-700 flex gap-2">
          <input className="input flex-1" value={input} onChange={e=>setInput(e.target.value)} onKeyDown={(e)=>{ if (e.key === 'Enter') sendMsg() }} placeholder="Write a message..." />
          <button className="btn bg-blue-600 hover:bg-blue-700" onClick={sendMsg}><Send className="w-4 h-4"/></button>
        </div>
      </div>
    </div>
  )
}
